<script>
import {sleep} from '../utils/timeSleep'
export default {
  name: 'HelloWorld',
  data(){
    return {
      curNotes: [],
      userNotes: [],
      difficult: 1500,
      round: 0,
      colors: ['yellow','red','green','blue'],
      gameActive: false,
      lost: false,
      win: false
    }
  },
  methods: {
    start(){
      this.restartGame()
      this.startRound()
    },
    async startRound(){
      if (this.round < 7){
        this.gameActive = false
        this.round++
        let randNumber = Math.floor((Math.random() * 4))
        this.curNotes.push(this.colors[randNumber])
        this.userNotes = [...this.curNotes]
        for (let color of this.curNotes){
        await sleep(+this.difficult)
        this.triggerTile(color)
      }
      this.gameActive = true
      }else{
        this.endGame()
      }
    },
    endGame(){
      if (this.round == 7 && this.userNotes.length == 0) {
          this.win = true
        }else{
          this.lost = true
        }
        this.gameActive = false
    },
    registerClick(e){
    //   if (!this.gameActive) return
      const color = e.target.id
      this.playSound(color)
    //   if (this.userNotes[0] == color){
    //     this.userNotes.shift()
    //     }else{
    //     this.endGame()
    //     }
    //   this.changeColor(color)
    // //   if (this.userNotes.length == 0){
    // //     //   this.startRound()
    // //     }
      },
    triggerTile(color){
      this.playSound(color)
      this.changeColor(color)
    },
    restartGame(){
      this.round = 0
      this.curNotes = []
      this.userNotes = []
      this.lost = false
      this.win = false
    },
    playSound(color){
      let audio = new Audio(require(`../sounds/${this.colors.indexOf(color)+1}.ogg`))
      audio.play()
    },
    async changeColor(color){
      const tile = document.querySelector(`#${color}`)
      tile.className = `tile ${color} active`
      await sleep(200)
      tile.className = `tile ${color}`
    }
    },
    watch: {
      difficult: function() {
        this.restartGame()
      }
    }
}
</script>

